.p-contents {
  background-image: url(../img/p-body_2.jpg);
  background-size: contain;
  background-size: cover;
}

.p-header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 80;
  width: 100%;
  height: 60px;
  font-family: "Architects Daughter", cursive;
  transition: all 0.2s;
  overflow: hidden;
}

.p-header__wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 35px;
  margin-right: 40px;
}

.p-header__nav__block {
  margin-top: -10px;
  width: 40%;
  height: 25px;
}

.p-header__nav {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  margin-right: 35%;
}

.p-header__nav__item_label {
  padding: 10px 15px;
  text-decoration: none;
  color: aliceblue;
  font-size: 25px;
  font-family: "Architects Daughter", cursive;
  font-weight: bold;
}

.p-footer {
  width: 100%;
  height: 60px;
  padding-top: 2.5%;
  background-color: #ffc9d2;
}

.p-footer__menu {
  text-align: center;
}

.p-footer__menu__copy {
  font-family: "Architects Daughter", cursive;
  color: gray;
  font-weight: bold;
}

.p-hero {
  position: relative;
  z-index: 40;
  width: 100%;
  height: 0;
  padding-bottom: 46.25%;
  overflow: hidden;
}

.p-hero__label {
  position: absolute;
  z-index: 80;
  top: 250px;
  right: 10%;
  font-size: 60px;
  font-family: "Architects Daughter", cursive;
  color: white;
}

.p-hero__mov {
  position: absolute;
  z-index: 60;
  top: 0;
  left: 0;
  width: 100%;
}

.black {
  position: absolute;
  z-index: 70;
  width: 100%;
  height: 100%;
  background-color: #161616;
  opacity: 0.6;
}

#wrapper {
  position: absolute;
  z-index: 200;
  width: 100%;
  height: auto;
}

#effect {
  position: absolute;
  z-index: 200;
  width: 100%;
}

#curtain1 {
  position: absolute;
  z-index: 200;
  top: 0px;
  left: 0px;
  width: 50%;
}

#curtain2 {
  position: absolute;
  z-index: 200;
  top: 0px;
  right: 0;
  width: 50%;
}

#curtain_buttons {
  display: block;
  width: 100%;
  margin-top: 25px;
}

#curtain_buttons input[type=button] {
  width: 100px;
  height: 25px;
  border-radius: 2px;
  color: white;
  background-color: #ffc9d2;
  border: none;
  border-bottom: 6px #ffc9d2;
}

.p-button__wrapper {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
  margin-right: 5%;
  margin-bottom: 60px;
  transition: all 0.2s;
}

.p-button {
  width: 20%;
  height: 35px;
  padding: 5px 10px;
  background-color: #e2f020;
  border-radius: 65px;
  border: none;
  transition: all 0.2s;
}

.c-btn-label {
  font-size: 20px;
  color: aliceblue;
  font-family: "Architects Daughter", cursive;
  font-weight: bold;
}